import Vue from 'vue';
import isPlainObject from 'lodash/isPlainObject';
import has from 'lodash/has';
import get from 'lodash/get';
import forEach from 'lodash/forEach';
import intersection from 'lodash/intersection';
import includes from 'lodash/includes';
import isArray from 'lodash/isArray';
import xor from 'lodash/xor';
import { saferEval, set, unset } from './lib/util';

export default class Space {
    constructor(id, data = null, methods = null) {
        if (id === 'undefined') {
            throw 'Space id is required.';
        }

        this.id = id;
        this.$data = data ? data : {};
        this.$methods = methods ? methods : {};
    }

    setData(data) {
        Vue.set(this, '$data', data);
    }

    setMethods(methods) {
        const self = this;

        if (isPlainObject(methods)) {
            forEach(methods, function(method, name) {
                Vue.set(self.$methods, name, method);
            });
        }
    }

    setWatchers(watchers, context) {
        const self = this;

        forEach(saferEval(watchers, context, { space: self }), (method, attribute) => {
            context.$watch(function() {
                return self.get(attribute);
            }, method);
        });
    }

    unset(key) {
        unset(this.$data, key);
    }

    get(key) {
        return get(this.$data, key);
    }

    has(key) {
        return has(this.$data, key);
    }

    includes(key, value) {
        if (isArray(value)) {
            return intersection(this.get(key), value).length === value.length;
        }

        return includes(this.get(key), value);
    }

    call(key, ...args) {
        if (typeof this.$methods[key] !== 'undefined') {
            this.$methods[key].bind(this)(...args);
        }
    }

    set(key, val) {
        if (isPlainObject(key)) {
            forEach(key, (objVal, objKey) => {
                this.set(objKey, objVal);
            });

            return;
        }

        if (typeof val === 'function') {
            return set(this.$methods, key, val);
        } else {
            return set(this.$data, key, val);
        }
    }

    toggle(key, val = null) {
        let currentVal = this.get(key);

        if (typeof currentVal === 'boolean') {
            this.set(key, !currentVal);
        }

        if (isArray(currentVal)) {
            this.set(key, xor(currentVal, [val]));
        }
    }
}
