import { saferEval } from './lib/util';
import SpaceManager from './SpaceManager';

export default {
    install(Vue) {
        const manager = new SpaceManager();

        window.__v_space_manager = Vue.observable(manager);

        Vue.mixin({
            methods: {
                $space(id) {
                    return this.$spaces.getSpace(id);
                },
            },
            computed: {
                $spaces() {
                    return window.__v_space_manager;
                },
            },
        });

        Vue.directive('space', {
            bind(el, binding, vnode) {
                if (binding.arg === 'undefined') {
                    throw 'Space directive requires argument as space id';
                }
                if (binding.modifiers.init) {
                    vnode.context.$nextTick(() => {
                        saferEval(binding.expression, vnode.context, {
                            space: binding.arg ? window.__v_space_manager.makeSpace(binding.arg) : {},
                        })();
                    });
                } else {
                    let space = window.__v_space_manager.makeSpace(binding.arg);

                    if (binding.modifiers.watch) {
                        space.setWatchers(binding.expression, vnode.context);
                    } else if (binding.modifiers.methods) {
                        space.setMethods(saferEval(binding.expression, vnode.context, { space: space }));
                    } else {
                        space.setData(saferEval(binding.value));
                    }
                }
            },
        });

        Vue.component('space', {
            props: {
                id: {
                    type: String,
                },
                init: {
                    type: String,
                    default: null,
                },
                methods: {
                    type: [String, Object],
                    default: null,
                },
                watch: {
                    type: [String, Object],
                    default: null,
                },
                data: {
                    type: [String, Object],
                    default: null,
                },
            },
            functional: true,
            render(createElement, context) {
                let space = window.__v_space_manager.makeSpaceFromFunctionalContext(context);

                space.setWatchers(context.props.watch, context.parent);

                if (context.data.tag || context.props.tag) {
                    return createElement(context.data.tag || context.props.tag, context.data, context.scopedSlots.default(space));
                }

                return context.scopedSlots.default(space);
            },
        });
    },
};
