import Vue from 'vue';
import Space from './Space';
import { saferEval, saferEvalNoReturn } from './lib/util';

export default class SpaceManager {
    constructor() {
        this.spaces = {};
        this.emptySpace = new Space();
    }

    getSpace(id) {
        if (typeof this.spaces[id] === 'undefined') {
            return this.emptySpace;
        }

        return this.spaces[id];
    }

    makeSpaceFromFunctionalContext(context) {
        return this.makeSpace(context.props.id, context.props.data, context.props.methods, context.props.watch, context.props.init);
    }

    makeSpace(id, data, methods, watchers, init = '', context) {
        if (typeof id !== 'string') {
            throw 'An id is required to create a space';
        }

        if (typeof this.spaces[id] !== 'undefined') {
            return this.spaces[id];
        }

        if (typeof context === 'undefined') {
            context = this;
        }

        let space = Vue.observable(new Space(id, data, methods, watchers));

        if (data) {
            space.setData(saferEval(data, context));
        }

        if (methods) {
            space.setMethods(saferEval(methods, context));
        }

        if (watchers) {
            space.setWatchers(watchers, context);
        }

        if (init) {
            saferEvalNoReturn(init, {
                space: space,
            });
        }

        Vue.set(this.spaces, id, space);

        return this.spaces[id];
    }
}
